<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  mounted:function(){
    let aa = sessionStorage.getItem("loginInfo")
    aa = JSON.parse(aa);
    if(aa){
      return
    }else{
      // console.log(aa.token)
      this.$router.push({path:'/login'})
    }
  }
}
</script>

<style>
    #app {
        font-family: PingFang-SC-Medium, Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        width:100%;
        height:100%;
        background: #fff;
    }
    *{
        margin:0;
        padding:0;
    }
    html,body{
        width:100%;
        height:100%;
    }
    p{
        font-family: PingFang-SC-Medium, Helvetica, Arial, sans-serif;
    }
    ul{list-style:none}
    a{text-decoration: none;color:#000000}
    img{vertical-align:top;border:none}

    .clearf:after,
    .clearf:before{
        content:"";
        display:block;
        clear:both;
    }
    input{
        border:none;
        outline: none;
    }
</style>



