<template>
    <div class="chatHome">
        <router-view></router-view>
        <mt-tabbar v-model="selected">
                <mt-tab-item id="message">
                    <div @click="toMessage">
                        <img slot="icon" src="@/static/image/chat/message.png">
                        消息
                    </div>
                </mt-tab-item>
                <mt-tab-item id="friend">
                    <div @click="toLinkman">
                        <img slot="icon" src="@/static/image/chat/friend.png">
                        联系人
                    </div>

                </mt-tab-item>
                <mt-tab-item id="animate">
                    <div @click="toAnimate">
                        <img slot="icon" src="@/static/image/chat/animate.png">
                        动态
                    </div>
                </mt-tab-item>
                <mt-tab-item id="center">
                    <div @click="toCenter">
                        <img slot="icon" src="@/static/image/chat/center.png">
                        我的
                    </div>
                </mt-tab-item>
        </mt-tabbar>
    </div>
</template>

<script>
  import { MessageBox } from 'mint-ui';
  export default {
    name: "chatHome",
    data(){
      return{
        selected:'message',
        loginInfo:{}
      }
    },
    created(){
      // this.selected = window.localStorage.getItem('selected')
      // console.log(this.selected)
      // this.loginInfo=sessionStorage.getItem("loginInfo");
      let aa = sessionStorage.getItem("loginInfo")
      aa = JSON.parse(aa);
      if(aa){

      }else{
        // console.log(aa.token)
        this.$router.push({path:'/login'});
          return;
      }
      let registType = JSON.parse(window.localStorage.getItem('personRegistInfo'));
      // console.log(registType.info)
      let pop = JSON.parse(window.localStorage.getItem('popInfo'));
      // console.log(typeof pop!='undefined')
      if(pop){

      }else{
        MessageBox({
          title: '继续完善信息',
          message: '确定执行此操作?',
          showCancelButton: true
        });
        window.localStorage.setItem('popInfo',JSON.stringify({"info":0}))
        if(registType.info==0){
          MessageBox.confirm('立即完善信息？').then(action=>{
            this.$router.push('/loginInfo2')
          })
        }else if(registType.info==1){
          MessageBox.confirm('立即完善信息？').then(action=>{
            this.$router.push('/loginInfo')
          })
        }
      }
    },
    methods:{
      toMessage(){
        this.$router.push({path:'/message'})
        // window.localStorage.setItem('selected',this.selected)
      },
      toLinkman(){
        this.$router.push({path:'/linkman'})
        // window.localStorage.setItem('selected',this.selected)
      },
      toAnimate(){
        this.$router.push({path:'/animated'})
        // window.localStorage.setItem('selected',this.selected)
      },
      toCenter(){
        this.$router.push({path:'/center'})
        // window.localStorage.setItem('selected',this.selected)
      }
    },
      watch:{
        $route(to,form){
            console.log(to.path)
        }
      }
  }
</script>

<style scoped>
    .mint-tabbar > .mint-tab-item.is-selected{
        color:#000
    }
    .mint-tabbar{
        position: fixed;
    }
</style>



