<template>
    <div class="register2">
        <topNav title="企业注册"></topNav>
        <div class="list">
            <div class="item">
                <p>请上传公司logo</p>
                <div class="itemRight">
                    <img style="width:40px;" :src="imgUrl" alt="">
                    <img src="@/static/image/login/arrowRight.png" alt="">
                </div>
                <input class="file" type="file" accept="image/png,image/gif,image/jpg,image/jepg" @change="update"/>
            </div>
            <div class="item" @click="showPicker1=true">
                <p>输入您的职位</p>
                <div class="itemRight">
                    <p>{{item1}}</p>
                    <img src="@/static/image/login/arrowRight.png" alt="">
                </div>
            </div>
            <div class="item" @click="showPicker2=true">
                <p>请输入公司介绍</p>
                <div class="itemRight">
                    <p class="inputText">{{item2}}</p>
                    <img src="@/static/image/login/arrowRight.png" alt="">
                </div>
            </div>
            <div class="item" @click="showPicker3=true">
                <p>请输入经营范围</p>
                <div class="itemRight">
                    <p class="inputText">{{item3}}</p>
                    <img src="@/static/image/login/arrowRight.png" alt="">
                </div>
            </div>
            <div class="item" @click="showPicker4=true">
                <p>请选择企业目标</p>
                <div class="itemRight">
                    <p>{{item4}}</p>
                    <img src="@/static/image/login/arrowRight.png" alt="">
                </div>
            </div>
        </div>
        <div class="next" @click="doRegist">
            立即注册
        </div>

        <div class="mark" v-show="showPicker1" @click="showPicker1=false">
            <mt-picker class="picker" :slots="slot1" @change="onValuesChange1"  @click="showPicker1=true"></mt-picker>
        </div>
        <div class="mark" v-show="showPicker2" @click="click2"></div>
        <textarea v-show="showPicker2" v-model="item2" class="textArea" placeholder="请输入公司介绍"></textarea>

        <div class="mark" v-show="showPicker3" @click="showPicker3=false"></div>
        <textarea v-show="showPicker3" v-model="item3" class="textArea" placeholder="请输入经营范围"></textarea>

        <div class="mark" v-show="showPicker4" @click="showPicker4=false">
            <mt-picker class="picker" :slots="slot4" @change="onValuesChange4"  @click="showPicker4=true"></mt-picker>
        </div>
    </div>
</template>

<script>
  import { Toast } from 'mint-ui';
  export default {
    name: "companyRegist",
    data(){
      return {
        imgUrl:'',
        showPicker1:false,
        showPicker2:false,
        showPicker3:false,
        showPicker4:false,
        item1:'',
        item2:'',
        item3:'',
        item4:'',
        info:{},
        slot1:[
          {flex: 1, values: ['房地产', '看电影', '玩游戏'], className: 'slot1', textAlign: 'center'}
        ],
        slot4: [
          {flex: 1, values: ['打球', '看电影', '玩游戏'], className: 'slot1', textAlign: 'right'
          }
        ]
      }
    },
    created(){
      let info = JSON.parse(window.localStorage.getItem('companyRegist'))
      this.info = info;
      this.$axios.get('/business/label/list/1').then(res=>{
        let arr1 = [];
        let arr = res.data.data
        for(let item of arr){
          arr1.push(item.context)
        }
        this.slot1=[{flex: 1, values: arr1, className: 'slot1', textAlign: 'center'}]
      })
      this.$axios.get('/business/label/list/3').then(res=>{
        let arr1 = [];
        let arr = res.data.data;
        for(let item of arr){
          arr1.push(item.context)
        }
        this.slot4=[{flex: 1, values: arr1, className: 'slot1', textAlign: 'center'}]
      })
    },
    methods:{
      onValuesChange1(picker,values){
        this.item1 = picker.getValues().toString()
      },
      onValuesChange4(picker,values){
        this.item4 = picker.getValues().toString()
      },
      click2(){
        this.showPicker2 = false;
      },
      update(e){
        // console.log('self',self)
        // console.log('e：',e)
        let file = e.target.files[0];
        let param = new FormData();
        param.append('file',file,file.name);
        // param.append('chunk','0');
        // console.log('param：',param.get('file'))
        let config = {
          headers:{'Content-Type':'multipart/form-data'}
        };
        this.$axios.post('/business/upload',param,config)
          .then(res=>{
            Toast({message:res.data.message, position:top, duration:3000})
            this.imgUrl = 'http://13.231.212.214/business/image?image='+res.data.data
          })
      },
      doRegist(){
        var info = this.info;
        console.log(info)
        if(this.item2==''){
          Toast({message:'公司介绍不能为空', position:top, duration:3000});
          return
        }
        if(this.item3==''){
          Toast({message:'经营范围不能为空', position:top, duration:3000});
          return
        }
        this.$axios.post('/business/pass/save?area='+info.place+'&companyName='+info.userName+'&email='
          +info.email+'&password='+info.password+'&pid=0&type=1&position='+this.item1+'&username='+info.id
        +'&intro='+this.item2+'&businessScope='+this.item3).then(res=>{
          console.log(res);
          if(res.data.code==200){
            Toast({message:res.data.message, position:top, duration:3000});
            sessionStorage.setItem("loginInfo", JSON.stringify(res.data.data));
            this.$router.push({path:'/'})
          }else{
            Toast({message:res.data.message, position:top, duration:3000});
            return
          }
        })
      }
    }
  }
</script>

<style scoped>
    .list{
        padding-top: 40px;
    }
    .file{
        position: absolute;
        width:100%;
        height: 100%;
        top:0;
        left:0;
        z-index:5;
        background: #ddd;
        opacity: 0;
    }
    .item{
        display: flex;
        width:90%;
        margin:0 auto;
        align-items: center;
        justify-content: space-between;
        padding: 16px 0;
        border-bottom:1px solid #eee;
        position: relative;
    }
    .item p{
        font-size:16px;
        letter-spacing: 1px;
    }
    .itemRight{
        display: flex;
        align-items: center;
    }
    .itemRight p{
        font-size:15px;
        color:#999;
    }
    .itemRight img{
        width:8px;
        margin-left:10px;
    }
    .mark{
        width:100%;
        height: 100%;
        position: fixed;
        top:0;
        left:0;
        background: rgba(0,0,0,0.6);
    }
    .textArea{
        width:70%;
        height: 180px;
        display: block;
        position: absolute;
        top:100px;
        left:10%;
        font-size:18px;
        padding: 5%;
    }
    .inputText{
        width:110px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        text-align: right;
    }
    .picker{
        background: #fff;
        position: fixed;
        bottom:0;
        left:0;
        width:100%;
    }
    .next{
        display: block;
        width:88%;
        margin:0 auto;
        margin-top:60px;
        background: #0094ff;
        border:none;
        outline: none;
        height: 50px;
        color:#fff;
        font-size:20px;
        border-radius:5px;
        text-align: center;
        line-height: 50px;
    }
    .next:active{
        background: #0083ee;
    }
</style>



