<template>
    <div class="message">
        <mt-header fixed title="消息">
            <img id="search" slot="left" src="@/static/image/chat/search.png" alt="">
            <img id="add" slot="right" @click="option" src="@/static/image/chat/add.png" alt="">
        </mt-header>
        <div class="option" v-show="showOption">
            <div class="optionItem">
                <img src="@/static/image/chat/addPeople.png" alt="">
                <p>添加好友</p>
            </div>
            <div class="optionItem">
                <img src="@/static/image/chat/scan.png" alt="">
                <p>扫一扫</p>
            </div>
            <div class="optionItem">
                <img src="@/static/image/chat/conversation.png" alt="">
                <p>发起聊天</p>
            </div>
            <div class="optionItem" style="border-bottom: none">
                <img src="@/static/image/chat/group.png" alt="">
                <p>发起群聊</p>
            </div>
            <div class="optionItem" style="border-bottom: none">
                <img style="width:17px;height: 8.5px;" src="@/static/image/chat/recommend.png" alt="">
                <p>推荐</p>
            </div>
        </div>
        <div class="event">
            <div class="eventItem">
                <img style="margin-top:40px;" src="@/static/image/chat/event1.png" alt="">
                <div style="margin-top:40px;">
                    <p>推荐</p>
                    <p>推荐人数：10人</p>
                </div>
            </div>
            <div class="eventItem" style="border-bottom: none">
                <img src="@/static/image/chat/event2.png" alt="">
                <div>
                    <p>待处理事项</p>
                    <p>11个请求待处理</p>
                </div>
            </div>
        </div>
        <div class="color"></div>
        <div class="event">
            <div class="eventItem">
                <img src="@/static/image/chat/event1.png" alt="">
                <div>
                    <p>英特尔中国</p>
                    <p>扩展大中华市，寻求商业合作机会大飒飒的爱迪生啊稍等</p>
                </div>
                <p class="time">14:45</p>
            </div>
        </div>
    </div>
</template>

<script>
  export default {
    name: "message",
    data(){
      return{
        num:0,
        showOption:false
      }
    },
    methods:{
      option(){
        let num = this.num;
        if(num%2==0){
          this.showOption = true
          this.num++
        }else{
          this.showOption = false
          this.num++
        }
      }
    }
  }
</script>

<style scoped>
    #search{
        width:18px;
        height: 18px;
    }
    #add{
        width:18px;
        height: 18px;
    }
    /*点击加号出现的选择项*/
    .option{
        position: fixed;
        width:140px;
        top:46px;
        right:6px;
        background: rgba(0,0,0,0.6);
        z-index:10;
    }
    .optionItem{
        display: flex;
        align-items: center;
        width:80%;
        margin:0 auto;
        padding: 10px 0;
        border-bottom:1px solid #efefef;
    }
    .optionItem img{
        width:17px;
        height: 17px;
        margin-left:8px;
    }
    .optionItem p{
        color:#efefef;
        margin-left:18px;
        letter-spacing: 1px;
        font-size:14px;
    }
    .event{
        width:100%;
    }
    .eventItem{
        display: flex;
        align-items: center;
        width: 90%;
        margin:0 auto;
        border-bottom: 1px solid #ddd;
        padding: 10px 0;
    }
    .eventItem img{
        width:50px;
        height: 50px;
        margin-left:5px;
    }
    .eventItem div p:first-child{
        margin-left:10px;
        font-size:17px;
        letter-spacing: 1px;
        font-family: "PingFang-SC-Bold";
    }
    .eventItem div p:last-child{
        margin-left:10px;
        font-size:14px;
        color:#888;
        letter-spacing: 1px;
        margin-top:4px;
        width:240px;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
    }
    .time{
        font-size:14px;
        color:#888;
        margin-top:-20px;
    }
    /*灰色隔块*/
    .color{
        width: 100%;
        height: 10px;
        background: #efefef;
    }
</style>



